/* =======================
      Login Card css
 ======================= */

.common-img-bg {
    height: 100%;
    background: url(../images/auth/authentication-bg.jpg) no-repeat center center fixed;
    background-size: cover;
}

.auth-body {
    min-width: 450px;
}

.login-card {
    border-radius: 30px;
    position: relative;
    display: block;
    // background-color: lighten($inverse-color,5%);
    h1 {
        margin-bottom: 20px;
        font-size: 25px;
        font-weight: 400;
        text-align: center;
    }
}

.login {
    text-align: left;
    ul li label {
        color: #757575;
        letter-spacing: 1px;
        font-weight: 600;
    }
    h3 {
        color: #666666;
        font-size: 24px;
        margin-top: 20px;
        font-weight: 600;
    }
}

.social-login {
    border-bottom: 1px solid #ccc;
    margin-bottom: 20px;
    padding-bottom: 30px;
    .btn {
        margin-left: 5px;
        margin-right: 5px;
    }
}

.login-card {
    .md-input-wrapper {
        margin-bottom: 30px;
    }
    .card-footer {
        background: #fff;
    }
    h1 {
        color: #1b8bf9;
    }
    .forget {
        border-bottom: 1px solid #fff;
        font-size: 24px;
        line-height: 0.6;
    }
    h3 {
        color: #666666;
        font-size: 24px;
        margin-top: 20px;
        font-weight: 600;
    }
}

.btn-signup,
.btn-singin1,
.btn-singin2 {
    .btn-md {
        font-size: 22px;
        font-weight: 400;
        width: 400px;
    }
}

.form-group-main .form-group {
    margin: 30px 0;
}

.login-card {
    .unlock,
    .btn-forgot {
        text-align: center;
    }
}

.imgs-screen .img-circle {
    padding: 4px;
    border: 1px solid #E6E6E4;
}

.sign-in-with {
    margin: 24px auto 16px;
    text-align: center;
    font-weight: 600;
    position: relative;
    .text {
        display: inline-block;
        vertical-align: baseline;
        position: relative;
        padding: 0 8px;
        z-index: 9999;
        &:after {
            left: 100%;
        }
        &:before {
            right: 100%;
        }
        &:after,
        &:before {
            content: '';
            display: block;
            width: 60px;
            position: absolute;
            top: 8px;
            border-top: 1px solid rgba(0, 0, 0, .28);
        }
    }
}

.error-500,
.error-503,
.error-404,
.error-400,
.error-403,
.login {
    position: fixed;
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    text-align: center;
    background-color: #2196F3;
}

.auth-box {
    margin: 20px 0 0 0;
    padding: 20px;
    border-radius: 8px;
    background-color: #fff;
    .confirm h3 {
        font-size: 34px;
        color: $primary-color;
    }
    i.icofont-check-circled {
        font-size: 42px;
    }
}

.btn-facebook {
    background-color: #3c5a99;
    border-color: #3c5a99;
    color: #fff;
    cursor: pointer;
    border-radius: 4px;
}

.btn-twitter {
    background-color: #41abe1;
    border-color: #41abe1;
    color: #fff;
    cursor: pointer;
    border-radius: 4px;
}

.footer {
    position: absolute;
    bottom: 0;
    width: 100%;
}

.footer p {
    width: 100%;
    margin-bottom: 0;
    padding: 18px;
}

.login-card-modal {
    margin: 0 auto;
}

.login.login-modal-main {
    background-color: #fff;
}

.mixed-form .tabbed-modal .nav-link h6 {
    padding: 10px 0;
}

.mixed-form .tabbed-modal .nav-link {
    border-radius: none;
    background-color: #f5f5f5;
    color: #888;
}

.mixed-form .tabbed-modal .nav-link.active {
    background-color: transparent;
    color: $inverse-color;
    border-color: transparent;
}

.mixed-form .modal-body {
    padding: 0;
}

.mixed-form .auth-box {
    margin: 10px;
    padding: 10px;
    border-radius: 8px;
    background-color: #fff;
    h3 {
        color: #666666;
        font-size: 22px;
        margin-top: 20px;
        font-weight: 600;
    }
    i.icofont-check-circled {
        font-size: 42px;
    }
}

.auth-box-main .navbar-nav li {
    color: #fff;
    line-height: 40px;
}

.offline-404 {
    background-color: #f3f3f3;  
    .auth-body{
        margin-top: -100px;
    }  
    h1 {
        letter-spacing: 5px;
        font-size: 160px;
        font-weight: 600;
        color: #2c3e50;
        text-shadow:3px -2px 4px rgba(128, 128, 128, 0.57);
    }
    h2{
        color: #aaa;
        font-weight: 600;
        margin-top: 30px;
        margin-bottom: 40px;
    }
}


.login.header{
    align-items: flex-start;
}
.header .login-card{
    margin-top: 30px;
}