@media only screen and (max-width: 992px) {
    .header-navbar {
        .navbar-wrapper {
            .navbar-container {
                margin-left: 0;
            }
            .navbar-logo {
                width: 100%;
                .mobile-menu {
                    display: block;
                    position: absolute;
                    left: 30px;
                }
                .mobile-search {
                    display: block;
                    position: absolute;
                    left: 70px;
                }
                .mobile-options {
                    display: block;
                    position: absolute;
                    right: 30px;
                }
            }
        }
        .navbar-container {
            .nav-left {
                display: none;
            }
        }
    }
    .animation-image img {
        margin: 20px auto 0;
    }
    .animation-type {
        text-align: center;
    }
   
}

@media only screen and (max-width: 768px) {
    .card h5{
        font-size: 16px;
    }
    .header-navbar .navbar-wrapper .navbar-logo {
        display: inherit;
        padding-top: 18px;
        a{
            display: inline-block;
        }
        .mobile-options {
            top: 18px;
        }
    }
    .main-body .page-wrapper .page-header-title {
        display: block;
    }
    body.header-fixed .main-body .page-wrapper {
        padding-bottom: 0;
        padding-top: 15px;
    }
    .main-body .page-wrapper .page-header {
        margin-bottom: 10px;
    }
    .main-body .page-wrapper .page-header-breadcrumb {
        float: inherit;
        display: inline-block;
        margin-top: 10px;
    }
    .card-header-right i:nth-child(n+2) {
        display: none;
    }
}

@media only screen and (max-width: 576px) {
    .header-navbar .navbar-wrapper .navbar-container .header-notification .show-notification {
        min-width: 280px;
        left: 0;
        right: auto;
        width: auto;
    }
    .header-navbar .navbar-wrapper .navbar-container .header-notification .profile-notification {
        left: auto;
        right: 0;
    }    
}


