/*===== Basic form elements start =====*/

.col-form-label {
    font-size: 14px;
}

.form-group {
    margin-bottom: 1.25em;
    .form-control-lg {
        font-size: 1.25em;
    }
    .form-control-round {
        border-radius: 50px;
    }
    .form-control-static {
        &:focus {
            outline: none;
        }
    }
}

.form-control {
    font-size: 14px;
    border-radius: 2px;
    &:focus {
        border-color: $primary-color !important;
    }
    &.form-control-normal {
        font-weight: 400;
    }
    &.form-control-bold {
        font-weight: 600;
    }
    &.form-control-capitalize {
        text-transform: capitalize;
    }
    &.form-control-uppercase {
        text-transform: uppercase;
    }
    &.form-control-lowercase {
        text-transform: lowercase;
    }
    &.form-control-variant {
        font-variant: small-caps;
    }
    &.form-control-left {
        text-align: left;
    }
    &.form-control-center {
        text-align: center;
    }
    &.form-control-right {
        text-align: right;
    }
    &.form-control-rtl {
        direction: rtl;
    }
}

// Form border colors

/*===== Border Inputs start =====*/

$form-border-state:primary,
warning,
default,
danger,
success,
inverse,
info;
$form-border-color:$primary-color,
$warning-color,
$default-color,
$danger-color,
$success-color,
$inverse-color,
$info-color;
@each $var in $form-border-state {
    $i: index($form-border-state, $var);
    .form-control-#{$var} {
        border-color: nth($form-border-color, $i);
        &:focus {
            border-color: nth($form-border-color, $i);
        }
    }
    .input-group-#{$var} {
        .input-group-addon {
            background-color: nth($form-border-color, $i);
        }
        .form-control {
            border-color: nth($form-border-color, $i);
        }
    }
}


/*===== Border Inputs ends =====*/

// Form input text color

/*===== Form Input-Text start =====*/

$form-txt-state:primary,
warning,
default,
danger,
success,
inverse,
info;
$form-txt-color:$primary-color,
$warning-color,
$default-color,
$danger-color,
$success-color,
$inverse-color,
$info-color;
@each $var in $form-txt-state {
    $i: index($form-txt-state, $var);
    .form-txt-#{$var} {
        color: nth($form-txt-color, $i);
        &:focus {
            color: nth($form-txt-color, $i);
        }
        &::-moz-placeholder {
            color: nth($form-txt-color, $i);
            opacity: 1
        }
        &:-ms-input-placeholder {
            color: nth($form-txt-color, $i);
        }
        &::-webkit-input-placeholder {
            color: nth($form-txt-color, $i);
        }
    }
}


/*===== Form Input-Text end =====*/

// Form Background Input

/*===== Form Background-inputs start =====*/

$form-bg-state:primary,
warning,
default,
danger,
success,
inverse,
info;
$form-bg-color:$primary-color,
$warning-color,
$default-color,
$danger-color,
$success-color,
$inverse-color,
$info-color;
@each $var in $form-bg-state {
