/*===== Radio Button css starts =====*/

.form-radio {
    position: relative;
    .form-help {
        position: absolute;
        width: 100%;
    }
    label {
        position: relative;
        padding-left: 1.5rem;
        text-align: left;
        color: #333;
        display: block;
    }
    input {
        width: auto;
        opacity: 0.00000001;
        position: absolute;
        left: 0;
    }
}

.radio {
    .helper {
        position: absolute;
        top: -0.15rem;
        left: -0.25rem;
        cursor: pointer;
        display: block;
        font-size: 1rem;
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
        color: #999;
        &::after {
            transform: scale(0);
            background-color: $primary-color;
            border-color: $primary-color;
        }
        &::after,
        &::before {
            content: '';
            position: absolute;
            left: 0;
            top: 3px;
            margin: 0.25rem;
            width: 1rem;
            height: 1rem;
            -webkit-transition: -webkit-transform 0.28s ease;
            transition: -webkit-transform 0.28s ease;
            transition: transform 0.28s ease, -webkit-transform 0.28s ease;
            border-radius: 50%;
            border: 0.125rem solid $primary-color;
        }
    }
    label:hover .helper {
        color: $primary-color;
    }
    input {
        &:checked ~ .helper::after {
            transform: scale(0.5);
        }
        &:checked ~ .helper::before {
            color: $primary-color;
        }
    }
    &.radiofill {
        input:checked ~ .helper::after {
            transform: scale(1);
        }
        .helper::after {
            background-color: $primary-color;
        }
    }
    &.radio-disable {
        label {
            cursor: not-allowed;
        }
        opacity:0.7;
    }
}

.radio-inline {
    display: inline-block;
    margin-right: 20px;
}

$radio-bg-state:primary,
warning,
default,
danger,
success,
inverse,
info;
$radio-bg-color:$primary-color,
$warning-color,
$default-color,
$danger-color,
$success-color,
$inverse-color,
$info-color;
@each $var in $radio-bg-state {
    $i: index($radio-bg-state, $var);
    .radio {
        &.radiofill {
            &.radio-#{$var} {
                .helper::after {
                    background-color: nth($radio-bg-color, $i);
                    border-color: nth($radio-bg-color, $i);
                }
                .helper::before {
                    border-color: nth($radio-bg-color, $i);
                }
            }
        }
    }
}


/*===== Radio Button css ends =====*/


/*===== Border-checkbox css start =====*/

.border-checkbox-section {
    .border-checkbox-group {
        display: inline-block;
        .border-checkbox:checked + .border-checkbox-label:after {
            -webkit-animation: check linear 0.5s;
            animation: check linear 0.5s;
            opacity: 1;
            border-color: $primary-color;
            .border-checkbox-label {
                &:before {
                    border-color: #eee;
                }
            }
        }
        .border-checkbox-label {
            position: relative;
            display: inline-block;
            cursor: pointer;
            height: 25px;
            line-height: 25px;
            padding-left: 30px;
            margin-right: 15px;
            &:after {
                content: "";
                display: block;
                width: 5px;
                height: 15px;
                opacity: .9;
                border-right: 2px solid #eee;
                border-top: 2px solid #eee;
                position: absolute;
                left: 5px;
                top: 15px;
                -webkit-transform: scaleX(-1) rotate(135deg);
                transform: scaleX(-1) rotate(135deg);
                -webkit-transform-origin: left top;
                transform-origin: left top;
            }
            &:before {
                content: "";
                display: block;
                border: 2px solid $primary-color;
                width: 25px;
                height: 25px;
                position: absolute;
                left: 0;
            }
        }
    }
    .border-checkbox {
        display: none;
        &:disabled {
            ~ .border-checkbox-label {
                cursor: no-drop;
                color: #ccc;
            }
        }
    }
}

@include theme-keyframes(check) {
    0% {
        height: 0;
        width: 0;
    }
    25% {
        height: 0;
        width: 5px;
    }
    50% {
        height: 15px;
        width: 5px;
    }
}

$border-checkbox-bg-state:primary,
warning,
default,
danger,
success,
inverse,
info;
$border-checkbox-bg-color:$primary-color,
$warning-color,
$default-color,
$danger-color,
$success-color,
$inverse-color,
$info-color;
@each $var in $border-checkbox-bg-state {
    $i: index($border-checkbox-bg-state, $var);
    .border-checkbox-section .border-checkbox-group-#{$var} {
        .border-checkbox-label:before {
            border: 2px solid nth($border-checkbox-bg-color, $i);
        }
        .border-checkbox:checked + .border-checkbox-label:after {
            border-color: nth($border-checkbox-bg-color, $i);
        }
    }
}


/*===== Border-checkbox css ends =====*/


/*===== Fade-in-checkbox css start =====*/

.checkbox-fade {
    display: inline-block;
    margin-right: 15px;
    label {
        line-height: 25px;
        input[type="checkbox"],
        input[type="radio"] {
            display: none;
            &:checked + .cr > .cr-icon {
                transform: scale(1) rotateZ(0deg);
                opacity: 1;
            }
            + .cr > .cr-icon {
                transform: scale(3) rotateZ(-20deg);
                opacity: 0;
                transition: all 0.3s ease-in;
            }
        }
        &:after {
            content: '';
            display: table;
            clear: both;
        }
    }
    &.fade-in-disable {
        label,
        .cr {
            color: #ccc;
            cursor: no-drop;
        }
    }
    .cr {
        border-radius: 0px;
        border: 2px solid $primary-color;
        cursor: pointer;
        display: inline-block;
        float: left;
        height: 25px;
        margin-right: 0.5em;
        position: relative;
        width: 25px;
        .cr-icon {
            color: $primary-color;
            font-size: 0.8em;
            left: 0;
            line-height: 0;
            position: absolute;
            right: 0;
            text-align: center;
            top: 50%;
        }
    }
}

$fade-in-checkbox-bg-state:primary,
warning,
default,
danger,
success,
inverse,
info;
$fade-in-checkbox-bg-color:$primary-color,
$warning-color,
$default-color,
$danger-color,
$success-color,
$inverse-color,
$info-color;
@each $var in $fade-in-checkbox-bg-state {
    $i: index($fade-in-checkbox-bg-state, $var);
    .checkbox-fade {
        &.fade-in-#{$var} .cr {
            border: 2px solid nth($fade-in-checkbox-bg-color, $i);
            .cr-icon {
                color: nth($fade-in-checkbox-bg-color, $i);
            }
        }
    }
}


/*===== Fade-in-checkbox css ends =====*/


/*===== Color-checkbox css start =====*/

.checkbox-color {
    display: inline-block;
    margin-right: 20px;
    cursor: pointer;
    label {
        display: inline-block;
        position: relative;
        padding-left: 10px;
        line-height: 25px;
        &::before {
            content: "";
            display: inline-block;
            position: absolute;
            top: 3px;
            width: 25px;
            height: 25px;
            left: 0;
            right: 0;
            text-align: center;
            margin-left: -20px;
            border: 1px solid #ccc;
            border-radius: 0;
            background-color: #fff;
            transition: border 0.15s ease-in-out, color 0.15s ease-in-out;
        }
        &::after {
            display: inline-block;
            position: absolute;
            width: 16px;
            height: 16px;
            left: 0;
            top: 2px;
            margin-left: -17px;
            padding-left: 3px;
            padding-top: 1px;
            font-size: 11px;
            color: #fff;
        }
    }
    input[type="checkbox"] {
        opacity: 0;
        &:focus + label::before {
            outline: thin dotted;
            outline: 5px auto -webkit-focus-ring-color;
            outline-offset: -2px;
        }
        &:checked + label::after {
            font-family: 'FontAwesome';
            content: "\f00c";
        }
        &:disabled + label {
            opacity: 0.65;
            &::before {
                background-color: #eee;
                cursor: not-allowed;
            }
        }
    }
    &.checkbox-circle label::before {
        border-radius: 50%;
    }
    &.checkbox-inline {
        margin-top: 0;
    }
}

.checkbox-primary,
.checkbox-danger,
.checkbox-info,
.checkbox-warning,
.checkbox-success {
    input[type="checkbox"]:checked + label::after {
        color: #fff;
    }
}

.checkbox-primary input[type="checkbox"]:checked + label::before {
    background-color: #2196f3;
    border-color: #2196f3;
}

$background-checkbox-bg-state:primary,
warning,
default,
danger,
success,
inverse,
info;
$background-checkbox-bg-color:$primary-color,
$warning-color,
$default-color,
$danger-color,
$success-color,
$inverse-color,
$info-color;
@each $var in $background-checkbox-bg-state {
    $i: index($background-checkbox-bg-state, $var);
    .checkbox-#{$var} input[type="checkbox"]:checked + label::before {
        background-color: nth($background-checkbox-bg-color, $i)
    }
}


/*===== Color-checkbox css ends =====*/


