body {
    background-color: $theme-bg-color;
    font-size: $theme-font-size;
    overflow-x: hidden;
    font-family: $theme-font-family;
}

ul {
    padding-left: 0;
    list-style-type: none;
    margin-bottom: 0;
}

* a {
    font-size: 14px;
    &:hover,
    &:focus {
        text-decoration: none;
    }
}

p {
    font-size: 13px;
}


/*===== Main-body css starts =====*/

.main-body {
    .page-wrapper {
        padding: 1.8rem;
        margin-left: 240px;
        transition: all ease-in .3s;
        .page-header {
            margin-bottom: 20px;
        }
        .page-header-title {
            display: inline-block;
            h4 {
                display: inline-block;
                margin-bottom: 0;
                font-weight: 600;
                color: #4a6076;
                font-size: 20px;
            }
            span {
                font-size: 13px;
                color: #9f9f9f;
                display: block;
                margin-top: 5px;
            }
        }
        .page-header-breadcrumb {
            float: right;
        }
    }
}


/*===== Main-body css ends =====*/


/*===== Card-layout css starts =====*/

.button-page .card-block {
    margin-bottom: -20px;
    ul {
        margin-bottom: 0;
        display: inline-block;
    }
    a {
        margin-bottom: 20px;
    }
    ul li {
        display: inline-block;
        margin-right: 20px;
        float: left;
    }
}

.button-page .btn {
    margin-bottom: 20px;
    margin-right: 10px;
}

.btn {
    &.btn-round {
        border-radius: 2rem;
    }
    &.btn-square {
        border-radius: 0;
    }
    &.btn-skew {
        transform: skew(-15deg);
    }
    i {
        margin-right: 5px;
    }
    &.btn-icon {
        border-radius: 50%;
        width: 40px;
        line-height: 30px;
        height: 40px;
        padding: 3px;
        text-align: center;
    }
}
.btn-group, .btn-group-vertical{
    display: inline-block;
}
.btn-group .btn{
    float: left;
}
.icon-btn i {
    margin-right: 0 !important;
}

.card {
    border-radius: 2px;
    border-top: 4px solid #8CDDCD;
    box-shadow: 0 2px 1px rgba(0, 0, 0, .05);
    border: {
        left: none;
        right: none;
        bottom: none;
    }
    ;
    margin-bottom: 30px;
    .card-footer {
        background-color: $white-txt;
    }
    .card-header {
        background-color: transparent;
        padding: 15px 20px;
        .card-header-left {
            display: inline-block;
        }
        .card-header-right {
            border-radius: 0px 0px 0px 5px;
            right: 0;
            top: 0;
            background: #f6f7f7;
            display: inline-block;
            float: right;
            padding: 7px 0;
            position: absolute;
        }
        .card-header-right i {
            margin: 0 20px;
            cursor: pointer;
        }
        span {
            color: #9f9f9f;
            display: block;
            font-size: 13px;
            margin-top: 5px;
        }
    }
    h5 {
        margin-bottom: 0;
        color: #757575;
        font-size: 18px;
        font-weight: 600;
        text-transform: capitalize;
        display: inline-block;
        margin-right: 10px;
    }
    .card-block {
        table {
            tr {
                padding-bottom: 20px;
            }
        }
        .sub-title {
            font-size: 14px;
            font-weight: 600;
            letter-spacing: 1px;
        }
        code {
            background-color: #eee;
        }
        .dropdown-menu {
            top: 38px;
        }
        p {
            line-height: 25px;
        }
        a {
            &.dropdown-item {
                margin-bottom: 0;
                font-size: 14px;
                transition: 0.25s;
                .active,
                &:active {
                    background-color: $primary-color;
                }
            }
        }
        &.remove-label i {
            margin: 0;
            padding: 0;
        }
        &.button-list span.badge {
            margin-left: 5px;
        }
        .dropdown-menu {
            background-color: #fff;
            padding: 0;
            .dropdown-divider {
                background-color: #ddd;
                margin: 3px 0;
            }
        }
        .dropdown-menu > a {
            padding: 8px 16px;
            line-height: 1.429;
        }
        .dropdown-menu > li > a:hover,
        .dropdown-menu > li > a:focus {
            background-color: rgba(202, 206, 209, 0.5);
        }
        .dropdown-menu > li:first-child > a:first-child {
            border-top-right-radius: 4px;
            border-top-left-radius: 4px;
        }
        .badge-box {
            box-shadow: 0 1px 5px 0 rgba(0, 0, 0, 0.14);
            padding: 10px;
            margin: 12px 0;
        }
    }
    .inner-card-block {
        border: 1px solid #ddd;
    }
}

.show>.dropdown-menu {
    overflow: hidden;
}
.dropdown-danger, .dropdown-success, .dropdown-warning, .dropdown-info, .dropdown-warning, .dropdown-primary, .dropdown-default, .dropdown-inverse, .dropdown-disabled{
    display: inline-block;
    vertical-align: center;
    position: relative;
}
.dropdown-split [class*=btn-], .btn-group [class*=btn-]{
    margin-right:0;
}
.button-page .btn-group{
    margin-right: 10px;
}
.card-header-text {
    margin-bottom: 0;
    font-size: 1rem;
    color: rgba(51, 51, 51, 0.85);
    text-transform: uppercase;
    font-weight: 600;
    display: inline-block;
    vertical-align: middle;
}

.generic-card-body code {
    cursor: pointer;
    display: inline-block;
    margin-right: 10px;
    margin-bottom: 10px;
}

.generic-image-body {
    li {
        padding: 0 20px;
    }
    code {
        display: block;
    }
    img {
        display: block;
        margin: 10px auto 0;
    }
}


/*===== Card-layout css end =====*/


/*===== background color css starts =====*/

.bg-color-box {
    text-align: center;
    display: inline-block;
    padding: 10px 20px;
    box-shadow: 0 0 5px 0 rgba(128, 128, 128, 0.49);
    font-weight: 600;
    margin-right: 20px;
    margin-bottom: 20px;
    cursor: pointer;
}

.bg-color-box span {
    color: #fff;
}


/*===== background color css end =====*/


/*=======  Breadcrumb css start  ======  */

.nav-pills .nav-link.active,
.nav-pills .nav-link,
.nav-pills .nav-link {
    &.active,
    &.active:focus,
    &.active:hover {
        background: transparent;
        color: #0275d8;
        text-align: center;
        border-bottom: 1px solid #0275d8;
    }
}


/*=======  Breadcrumb css end  ======  */

.icofont-rounded-down {
    -webkit-transition: all ease-in .3s;
    display: inline-block;
    transition: all ease-in .3s;
}

.icon-up {
    -webkit-transform: rotate(180deg);
    transform: rotate(180deg);
}

.rotate-refresh {
    -webkit-animation: mymove .8s infinite linear;
    /* Safari 4.0 - 8.0 */
    animation: mymove .8s infinite linear;
    display: inline-block;
}

@-webkit-keyframes mymove {
    0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}


/* Standard syntax */

@keyframes mymove {
    0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}


/*===== Card-layout css ends =====*/

.popover-content {
    .color-code {
        margin-bottom: 10px;
    }
    .display-color {
        width: 75px;
        height: 2.50rem;
        border-radius: .25rem;
        margin-right: 30px;
        @include button-trans(theme-color(primary, trans), theme-color(primary, trans));
    }
    span.block {
        display: block;
        width: 100%;
        text-align: center;
        font-size: 14px;
    }
}

.popover-title {
    text-align: center;
}


/*====== Breadcrumb css starts ======*/

.breadcrumb-title {
    a {
        font-size: 14px;
        color: #4a6076;
    }
    li:last-child a {
        color: #7e7e7e;
    }
}


/*====== Breadcrumb css ends ======*/


/*====== label badge css start ======*/

.badge {
    border-radius: 10px;
    padding: 3px 7px;
}

.label.label-lg {
    padding: 8px 21px;
}

.label.label-md {
    padding: 6px 14px;
}

.badge-lg {
    padding: 5px 9px;
    font-size: 14px;
}

.badge-md {
    padding: 4px 8px;
    font-size: 14px;
}

span.badge {
    display: inline-block !important;
}

.button-list .btn {
    margin-bottom: 10px;
    margin-right: 30px;
}

.btn-default .badge {
    @include labels-badge();
}

.btn-primary .badge {
    @include labels-badge-inverse(theme-color(primary, base));
}

.btn-success .badge {
    @include labels-badge-inverse(theme-color(success, base));
}

.btn-info .badge {
    @include labels-badge-inverse(theme-color(info, base));
}

.btn-warning .badge {
    @include labels-badge-inverse(theme-color(warning, base));
}

.btn-danger .badge {
    @include labels-badge-inverse(theme-color(danger, base));
}

.sub-title {
    border-bottom: 1px solid rgba(204, 204, 204, 0.35);
    padding-bottom: 10px;
    margin-bottom: 20px;
    text-transform: uppercase;
    font-size: 14px;
    font-weight: 400;
    color: #2c3e50;
}


/*label badge css end*/


/* ========================
    Start Box Shadow Css
    =========================== */


/* css for z-depth for top only */

$depth-val:-5;
@for $i from 0 through 5 {
    .z-depth-top-#{$i} {
        @include shadow(0 #{$depth-val}px 25px -5px #ccc, 0 1px 5px 0 rgba(0, 0, 0, 0.1), 0 0 0 0 rgba(0, 0, 0, 0));
        @if($depth-val > -8) {
            $depth-val: $depth-val - 2;
        }
        @else {
            $depth-val: $depth-val - 1;
        }
    }
}


/* css for z-depth for bottom only */

$depth-val:5;
@for $i from 0 through 5 {
    .z-depth-bottom-#{$i} {
        @include shadow(0 #{$depth-val}px 25px -5px #ccc, 0 1px 5px 0 rgba(0, 0, 0, 0.1), 0 0 0 0 rgba(0, 0, 0, 0));
        @if($depth-val < 8) {
            $depth-val: $depth-val + 2;
        }
        @else {
            $depth-val: $depth-val + 1;
        }
    }
}


/* css for z-depth left only */

$depth-val:-5;
@for $i from 0 through 5 {
    .z-depth-left-#{$i} {
        @include shadow(#{$depth-val}px 0 25px -5px #ccc, 0 1px 5px 0 rgba(0, 0, 0, 0.1), 0 0 0 0 rgba(0, 0, 0, 0));
        @if($depth-val > -8) {
            $depth-val: $depth-val - 2;
        }
        @else {
            $depth-val: $depth-val - 1;
        }
    }
}


/* css for z-depth right only */

$depth-val:5;
@for $i from 0 through 5 {
    .z-depth-right-#{$i} {
        @include shadow(#{$depth-val}px 0 25px -5px #ccc, 0 1px 5px 0 rgba(0, 0, 0, 0.1), 0 0 0 0 rgba(0, 0, 0, 0));
        @if($depth-val < 8) {
            $depth-val: $depth-val + 2;
        }
        @else {
            $depth-val: $depth-val + 1;
        }
    }
}


/* css for z-depth all side */

$depth-val:5;
@for $i from 0 through 5 {
    .z-depth-#{$i} {
        @include shadow(0 0 25px #{$depth-val}px #ccc, 0 1px 5px 0 rgba(0, 0, 0, 0.1), 0 0 0 0 rgba(0, 0, 0, 0));
        @if($depth-val < 8) {
            $depth-val: $depth-val + 2;
        }
        @else {
            $depth-val: $depth-val + 1;
        }
    }
}

.box-list div div div {
    margin-bottom: 20px;
}

.box-list {
    padding-bottom: 0;
}

.box-list p {
    margin-bottom: 0 !important;
}


/* shadow css end */


/* rating css starts*/

.br-wrapper {
    margin: 20px 0 50px;
}

.br-theme-bars-1to10 .br-widget .br-current-rating {
    clear: both;
    padding: 0;
    font-size: 16px;
    color: #757575;
    font-weight: 600;
}

.br-theme-bars-reversed .br-widget .br-current-rating {
    clear: both;
    padding: 0;
