/*range slider start */

.range-slider {
    min-height: 200px;
    display: flex;
    align-items: center;
    margin-left: 15px;
}

#RGB {
    height: 10px;
    background: rgb(128, 128, 128);
}

#RC .slider-selection {
    background: #FF8282;
}

#RC .slider-handle {
    background: red;
}

#GC .slider-selection {
    background: #428041;
}

#GC .slider-handle {
    background: green;
}

#BC .slider-selection {
    background: #8283FF;
}

#BC .slider-handle {
    border-bottom-color: blue;
}

#R,
#G,
#B {
    width: 300px;
}

.md-input-wrapper {
    position: relative;
    padding-top: 4px;
    width: 100%;
    display: block;
    margin-bottom: 20px;
}

.range-slider-contain {
    position: absolute;
    bottom: 12%;
}

.slider-handle {
    top: -5px;
    background-image: linear-gradient(to bottom, #1abc9c 0%, #1abc9c 100%);
    box-shadow: none;
}

.slider-tick,
.slider-track {
    background-image: linear-gradient(to bottom, #BDC3C7 0%, #BDC3C7 100%)
}

.slider-handle.custom::before {
    line-height: 15px;
    font-size: 35px;
    color: #1abc9c;
}

#slider12a,
#slider12c {
    .slider-track-high {
        background: green;
    }
}

#slider12b,
#slider12c {
    .slider-track-low {
        background: red;
    }
}

#slider12c .slider-selection {
    background: yellow;
}

.slider-selection.tick-slider-selection {
    background-image: linear-gradient(to bottom, #28E1BD 0%, #28E1BD 100%);
}

.slider-tick.in-selection {
    background-image: linear-gradient(to bottom, #28E1BD 0%, #28E1BD 100%);
}

.slider.slider-horizontal {
    .slider-track {
        height: 2px;
    }
    .slider-tick,
    .slider-handle {
        top: -5px;
        cursor: pointer;
    }
}


/*range slider end */


