/*loader1*/

.loader {
    margin: 18px auto;
    top: 65px;
    left: 65px;
    right: 0;
    width: 100px;
    height: 100px;
    position: relative;
    transform: translate(-50%, -50%);
    &.animation-start .circle {
        animation: rotating 2s ease-in-out infinite;
    }
    &.animation-stop .circle {
        animation-iteration-count: 1;
    }
    .circle {
        width: 50px;
        height: 50px;
        display: block;
        position: absolute;
        transform-origin: 50% 50%;
        animation-iteration-count: 0;
    }
    .circle:after {
        content: '';
        top: 0;
        left: 0;
        width: 10px;
        height: 10px;
        display: block;
        border-radius: 50%;
        transform: translate(-50%, -50%);
        background-color: #1abc9c;
    }
    .circle.delay-1 {
        animation-delay: 0.125s;
    }
    .circle.size-1:after {
        width: 11px;
        height: 11px;
    }
    .circle.delay-2 {
        animation-delay: 0.25s;
    }
    .circle.size-2:after {
        width: 12px;
        height: 12px;
    }
    .circle.delay-3 {
        animation-delay: 0.375s;
    }
    .circle.size-3:after {
        width: 13px;
        height: 13px;
    }
    .circle.delay-4 {
