    }
    .circle.size-4:after {
        width: 14px;
        height: 14px;
    }
    .circle.delay-5 {
        animation-delay: 0.625s;
    }
    .circle.size-5:after {
        width: 15px;
        height: 15px;
    }
    .circle.delay-6 {
        animation-delay: 0.75s;
    }
    .circle.size-6:after {
        width: 16px;
        height: 16px;
    }
    .circle.delay-7 {
        animation-delay: 0.875s;
    }
    .circle.size-7:after {
        width: 17px;
        height: 17px;
    }
    .circle.delay-8 {
        animation-delay: 1s;
    }
    .circle.size-8:after {
        width: 18px;
        height: 18px;
    }
    .circle.delay-9 {
        animation-delay: 1.125s;
    }
    .circle.size-9:after {
        width: 19px;
        height: 19px;
    }
    .circle.delay-10 {
