    margin-bottom: 20px;
}

.h-active a {
    color: #1b8bf9 !important;
    font-weight: 600;
}

.img-circle {
    border-radius: 50%;
}

.b-none {
    border: none !important;
}


/**====== Tabs css end ======**/


/**====== Table css start ======**/

.table.table-xl {
