}

.table-columned {
    >tbody>tr {
        >td:first-child,
        >th:first-child {
            border-left: 0;
        }
    }
    >tfoot>tr {
        >td:first-child,
        >th:first-child {
            border-left: 0;
        }
    }
    >tbody>tr {
        >td,
        >th {
            border: 0;
            border-left: 1px solid #ddd;
        }
    }
    >tfoot>tr {
        >td,
        >th {
            border: 0;
