.img-overlay {
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    color: #fff;
    height: 100%;
    width: 60%;
    border-radius: 50%;
    -webkit-transition: all 0.4s ease-in-out;
    -o-transition: all 0.4s ease-in-out;
    transition: all 0.4s ease-in-out;
    position: absolute;
    transform: scale(0);
    margin: 0 auto;
    span {
        position: absolute;
        top: 42%;
        left: 30%;
        text-align: center;
    }
    i {
        margin-right: 0;
    }
}

.img-hover {
    position: relative;
    &:hover .img-overlay {
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        border-radius: 50%;
        transform: scale(1.02);
        margin: 0 auto;
        background-color: rgba(0, 0, 0, 0.7);
        z-index: 2;
        -webkit-transition: all 0.4s ease-in-out;
        -o-transition: all 0.4s ease-in-out;
        transition: all 0.4s ease-in-out;
    }
}

