        overflow: hidden;
        h5 {
            border-bottom: 1px solid #ccc;
            color: $primary-color;
            margin-top: 0;
            padding-bottom: 10px;
            margin-bottom: 15px;
            font-size: 18px;
            display: block;
        }
        .option-font {
            background-color: $primary-color;
            border-radius: 50%;
            bottom: 320px;
            color: #fff;
            cursor: pointer;
            display: inline-block;
            line-height: 35px;
            text-align: center;
            width: 35px;
        }
        .blog-detail {
            padding: 10px;
        }
    }
    .blog-img {
        transition: all 0.3s ease-in-out;
        overflow: hidden;
        margin-bottom: -20px;
        &:hover {
            opacity: 0.8;
            transition: all 0.3s ease-in-out;
        }
        .sharing {
            position: relative;
            bottom: 50px;
            left: 15px;
        }
        .share {
            background-color: rgba(0, 0, 0, 0.5);
            border-radius: 5px;
            bottom: 3px;
            color: #fff;
            cursor: pointer;
            display: inline-block;
            left: 50px;
            padding: 3px 5px;
            position: absolute;
        }
        .share::before {
            border-bottom: 6px solid transparent;
            border-left: 6px solid transparent;
            border-right: 6px solid rgba(0, 0, 0, 0.5);
            border-top: 6px solid rgba(0, 0, 0, 0.5);
            bottom: 8px;
            content: "";
            height: 42%;
            left: -6px;
            position: absolute;
            transform: rotate(-135deg);
            width: 12px;
        }
        .share i {
            color: #fff;
            font-size: 15px;
            border-right: 1px solid #fff;
            padding: 0 5px;
            &:last-child {
                border: none;
            }
            a {
                padding: 0 5px;
                color: #fff;
            }
        }
    }
}

