.wall-img-preview .wall-item:only-child,
.wall-img-preview .wall-item:first-child:nth-last-child(4) ~ div:nth-child(4),
.wall-img-preview .wall-item:first-child:nth-last-child(10) ~ div:nth-child(10) {
