}


/* Basic buttons css end */

.show>.btn-primary.dropdown-toggle {
    @include buttons-active(theme-color(primary, active), theme-color(primary, active));
}

.btn-disabled,
.dropdown-disabled,
.dropdown-split-disabled {
    cursor: not-allowed;
}


/* Outline buttons css start */

.btn-outline-primary {
    @include buttons-borders(theme-color(primary, active));
    background-color:transparent;
}

.btn-outline-warning {
    @include buttons-borders(theme-color(warning, active));
    background-color:transparent;
}

.btn-outline-default {
    @include buttons-borders(theme-color(default, active));
    background-color:transparent;
    &:hover {
        color: #fff;
    }
}

.btn-outline-danger {
    @include buttons-borders(theme-color(danger, active));
    background-color:transparent;
}

.btn-outline-success {
    @include buttons-borders(theme-color(success, active));
    background-color:transparent;
}

.btn-outline-inverse {
    @include buttons-borders(theme-color(inverse, active));
    background-color:transparent;
    &:hover {
        color: #fff;
    }
}

.btn-outline-warning {
    @include buttons-borders(theme-color(warning, active));
    background-color:transparent;
}

.btn-outline-info {
    @include buttons-borders(theme-color(info, active));
    background-color:transparent;
}

.btn-outline-disabled {
    @include buttons-borders(theme-color(disabled, active));
}


/* Outline buttons css end */


/* Button sizes css start */

.btn-xlg {
    padding: 14px 20px;
    font-size: 21px;
    line-height: 40px;
}

.btn-lg {
    padding: 12px 18px;
    font-size: 18px;
    line-height: 32px;
}

.btn-md {
    padding: 10px 16px;
    font-size: 15px;
    line-height: 23px;
}

.btn-sm {
    padding: 8px 14px;
    line-height: 16px;
    font-size: 11px;
}

.btn-mini {
    padding: 5px 10px;
    line-height: 14px;
    font-size: 10px;
}


/* Button sizes css end */


/*=======  social button css start  ======  */

.btn-facebook,
.btn-twitter,
.btn-linkedin,
.btn-dribbble,
.btn-google-plus,
.btn-instagram,
.btn-pinterest,
.btn-dropbox,
.btn-flickr,
.btn-tumblr,
.btn-skype,
.btn-youtube,
.btn-github {
    color: #fff;
    &:hover,
    &:focus {
        color: #fff;
        box-shadow: none;
