.main-menu {
    width: 240px;
    background: #2c3e50;
    float: left;
    z-index: 999;
    .main-menu-header {
        padding: 20px;
        background: #4a6076;
        display: flex;
        align-items: center;
        .user-details {
            display: inline-block;
            margin-left: 10px;
            #more-details {
                cursor: pointer;
            }
            span {
                color: #fff;
                font-size: 14px;
                display: block;
                text-transform: capitalize;
                i {
                    font-size: 10px;
                    margin-left: 10px;
                }
                &:first-child {
                    margin-bottom: 5px;
                }
            }
        }
    }
    .main-menu-content {
        .more-details {
            display: none;
        }
        .menu-search {
            padding: 20px;
            padding-bottom: 10px;
        }
        li {
            position: relative;
        }
        .nav-title {
            font-size: 13px;
            font-weight: 600;
            text-transform: uppercase;
            color: #2ed8b6;
            padding: 10px 15px;
            letter-spacing: 1px;
        }
        .nav-item.has-class {
            > a {
                background: #1ABC9C;
                &:hover {
                    color: #fff;
                }
            }
        }
        .tree-1.open .nav-sub-item {
            > a {
                background: #34495E;
            }
        }
        .tree-2.open .has-class {
            > a {
                color: #1ABC9C;
                font-weight: 600;
            }
        }
        .nav-item {
            color: #fff;
            ul li {
                transition: all ease-in .3s;
                transform: scale(0);
                opacity: 0;
            }
            .open {
                li {
                    transition: all ease-in .3s;
                    transform: scale(1);
                    opacity: 1;
                }
            }
            a {
                color: #fff;
                display: block;
                font-size: 13px;
                padding: 10px 15px;
                transition: all ease-in .2s;
                &:hover {
                    color: #1ABC9C;
                }
            }
            i {
                margin-right: 10px;
            }
            .icon-arrow-down {
                position: absolute;
                right: 15px;
                font-size: 10px;
                top: 15px;
            }
            .tree-1 {
                a {
                    @extend .nav-item;
                    padding-left: 30px;
                }
                i {
                    font-size: 10px;
                }
            }
            .tree-2 {
                @extend .tree-1;
                a {
                    padding-left: 50px;
                }
            }
        }
    }
}

.main-navigation {
    .nav-item,
    .nav-sub-item {
        .tree-1,
        .tree-2 {
            display: none;
        }
    }
    .nav-item.has-class,
    .nav-sub-item.has-class {
        .tree-1.open,
        .tree-2.open {
            display: block;
        }
    }
}

body.menu-collapsed {
    .slimScrollDiv,
    .main-menu-content {
        overflow: visible !important;
    }
    .main-menu-header,
    .main-menu .main-menu-content li.nav-title,
    .main-navigation .nav-item .tree-1,
    .main-menu .main-menu-content .main-navigation .nav-item .tree-2,
    .main-navigation .nav-item .tree-2,
    .nav-title,
    .nav-item span,
    .nav-item > a i.icon-arrow-down,
    .menu-search {
        display: none;
    }
    .main-menu {
        width: 70px;
    }
    .main-navigation {
        transition: all ease-in .3s;
        .nav-item {
            text-align: center;
            i {
                margin-right: 5px;
            }
            .tree-1 {
                display: block;
                left: 70px;
                opacity: 0;
                position: absolute;
                top: 0;
                transform: translateY(30px);
                visibility: hidden;
                width: 15em;
                -webkit-transform: translateY(30px);
                background: #34495e;
                transition: all linear 0.3s;
            }
            &:hover .tree-1 {
                -webkit-transform: translateY(0px);
                opacity: 1;
                transform: translateY(0px);
                transition: all linear 0.3s;
                visibility: visible;
            }
            .tree-1 li,
            .tree-1 a {
                text-align: left !important;
            }
        }
        .nav-sub-item {
            .tree-2 {
                a {
                    padding-left: 30px;
                }
                -webkit-transform: translateY(30px);
                background-color: #34495E;
                display: block !important;
                opacity: 0;
                position: absolute;
                top:0;
                left: 210px;
                width: 15em;
                transform: translateY(30px);
                transition: all linear 0.3s;
                visibility: hidden;
                z-index: 9999;
            }
            &:hover .tree-2 {
                -webkit-transform: translateY(0px);
                opacity: 1;
                transform: translateY(0px);
                transition: all linear 0.3s;
                visibility: visible;
            }
        }
    }
    .main-body .page-wrapper {
        margin-left: 70px;
        transition: all ease-in .3s;
    }

}

.menu-caption{
position: absolute;
	right:40px;
}
