.alert {
    font-size: 15px;
    margin-bottom: 2rem;
}

.alert.icons-alert {
    border-left-width: 48px;
    .alert-icon {
        position: relative;
        z-index: 3;
        text-align: center;
        top: 0;
        left: 15px;
    }
    p {
        margin-bottom: 0;
        line-height: 21px;
    }
}

.close {
    font-size: 16px;
    margin-top: 5px;
}


/*======= alert css starts ===========*/


/*alert primary*/

$alert-name:default,
primary,
success,
info,
warning,
danger;
$alert-color:$default-color,
$primary-color,
$success-color,
$info-color,
$warning-color,
$danger-color;
@each $var in $alert-name {
    $i: index($alert-name, $var);
    .alert-#{$var} {
        color: nth($alert-color, $i);
        border-color: nth($alert-color, $i);
        background-color: #fff;
    }
}


/*alert border */

$alert-name:default,
primary,
success,
info,
warning,
danger;
$alert-color:$default-color,
$primary-color,
$success-color,
$info-color,
$warning-color,
$danger-color;
@each $var in $alert-name {
    $i: index($alert-name, $var);
    .border-#{$var} {
        color: nth($alert-color, $i);
        border-color: transparent;
        /*  background-color: #f3f3f3; */
        box-shadow: 0 0 1px #999;
        border-left: 3px solid nth($alert-color, $i);
        border-radius: 0;
    }
}


/*========= solid alert background color ========*/

$alert-name:default,
primary,
success,
info,
warning,
danger;
$alert-color:$default-color,
$primary-color,
$success-color,
$info-color,
$warning-color,
$danger-color;
@each $var in $alert-name {
    $i: index($alert-name, $var);
    .background-#{$var} {
        color: #fff;
        background-color: nth($alert-color, $i);
    }
}

.icons-alert {
    position: relative;
    &:before {
        content: '\f027';
        position: absolute;
        font-family: 'IcoFont' !important;
        font-size: 16px;
        left: -30px;
        z-index: 999;
        color: #fff;
    }
}

[class*="alert-"] code {
    margin-left: 10px;
}


/*======= alert css ends ===========*/
