/*===== Menu css starts ===== */
.header-navbar{
	padding: 0;
	min-height: 4em;
	.navbar-wrapper{
		background-color: #2C3E50;
		.navbar-logo{
			height: 3.5rem;
			background: #34495E;
			width: 240px;
			display: flex;
			align-items: center;
			float: left;
			text-align: center;	
			justify-content: center;
			text-transform: uppercase;
			h5{
				margin-bottom:0;
			}
			a{
				color: #fff;
			}	
			.mobile-menu,.mobile-search,.mobile-options{
				display: none;				
			}	
		}
		.navbar-container{
			margin-left: 240px;
			.nav-left{
				margin-bottom: 0;
				float: left;
				li{
					float: left;					
					line-height: 4;
					padding: 0 10px;
					position: relative;
				}
				a{
					color: #fff;
					padding: 1.5rem .6rem;
				}
				.mega-menu-top{
					i{
						font-size: 10px;
						margin-left: 10px;
					}
				}
			}
			.nav-right{
				float: right;
				@extend .nav-left;
				.user-profile{
					img{
						width: 40px;
						margin-right: 10px;
					}
					i{
						font-size: 10px;
						margin-left: 10px;
					}
				}
			}
			.header-notification{
				perspective: 1000px;
				.badge{	
					position: absolute;
					background: red;
					border-radius: 100px;
					left: 27px;
					top: 10px;
				}				
				.show-notification{
					position: absolute;
					background: #fff;
					right: 0;
					visibility: hidden;
					opacity: 0;
					width: 24rem;					
					box-shadow:0 0 1px 2px rgba(0, 0, 0, 0.03), 0 -2px 1px -2px rgba(0, 0, 0, 0.02), 0 0 0 -1px rgba(0, 0, 0, 0.03);
					transition: all linear 0.3s;
					-webkit-transform: translateY(30px);
					transform: translateY(30px);
					a{
						color: $theme-font-color;
					}
					li{
						width: 100%;
						line-height: initial;
						border-top: 1px solid $border-color;	
						padding: 1em 20px;					
						&:first-child{
							padding: 10px 20px;
							h6{
								margin-bottom: 0;
								font-size: 14px;
								font-weight:600;
								text-transform:uppercase;
								display: inline-block;
							}
							label{
								float: right;
							}
						}
						img{
							@include width(40px);
							margin-right: 10px;
						}
					}
					.notification-user{
						font-size:15px;
						font-weight:bold;
					}
					.notification-msg{
						margin-bottom:5px;
					}
					.notification-time{
						font-size:12px;
					}
				}
				.profile-notification{
					@extend .show-notification;
					width: 17em;					
					a{
						padding-left: 0;
						font-size: 15px;
					}
					i{
						margin-left: 0;
						font-size: 15px;
						margin-right: 5px;
					}
					li{
						padding: .7em 20px;
						border-top: none;
						&:first-child{
							padding: .7em 20px;
						}
					}
				}
				&:hover .show-notification{
					visibility: visible;
					opacity: 1;	
					-webkit-transform: translateY(0px);
					transform:  translateY(0px);
				}
			}
			.mega-menu-top{
				@extend .header-notification;
				perspective: inherit;
				position: static !important;

				.show-notification{
					left: 0;
					width: calc(100% - 50px);
					margin:0 25px;
					z-index: 9999;
					li{
						padding:20px;
						&:first-child{
							padding: 20px;
							h6{
								display: block;
								border-bottom: 1px solid $border-color;
								padding-bottom: 10px;
								margin-bottom: 20px;
							}
						}
						h6{
							display: block;
							border-bottom: 1px solid #ddd;
							padding-bottom: 10px;
							margin-bottom: 20px;
							text-transform: uppercase;
							font-size: 14px;
							font-weight: 600;
						}
						img{
							width: auto;
							border-radius: 0;
							transition: all ease-in-out .3s;
							&:hover{
								box-shadow: 0px 0px 10px 2px rgba(128, 128, 128, 0.37);
							}
						}
					}
					.mega-mailbox{
						li{
							padding-top: 0;
							padding-left: 0;
							border-top:none;
							a{
								padding: 0;
								i{
									margin-left: 0;
									margin-right: 15px;
									font-size: 20px;
								}
							}
							h5{
								font-size: 15px;
								font-weight: 600;
							}
						}
					}
				}
				.mega-menu-links{
					li{
						border-top:none;
						padding: 10px 0;
						&:first-child{							
							padding: 10px 0;
							padding-top: 0;
						}
					}
					a{
						display: block;
						padding: 0;
						font-size: 14px;
						text-transform: capitalize;
					}
				}
				.mega-menu-contact{
					label,.form-control{
						font-size: 14px;
					}

				}
			}
		}
	}
}

/*===== Menu layouts css starts ===== */
body.fix-menu{
	.navbar-wrapper{
		position: fixed;
		top:0;
		width:100%;
		z-index: 9999;
	}
	.main-menu{
		position: fixed;
		left: 0;
		transition: all ease-in .3s;
	}
}
body.menu-static{
	.navbar-wrapper, .main-menu{
		position:initial;
	}
}
body.header-fixed{
	.navbar-wrapper{
		position: fixed;
		top:0;
		width:100%;
		z-index: 9999;
	}
	.main-menu{		
		position: fixed;
	}
	.main-body .page-wrapper{
		margin-left: 0;
	}
}
body.header-fixed.menu-hide{
	.main-menu{
		opacity:0;
		transition:all linear 300ms;
		transform: translateX(-240px);
	}
}
body.header-fixed.menu-show{
	.main-menu{
		opacity:1;
		transition:all linear 300ms;
		transform: translateX(0px);
	}
}
body.menu-compact{
	@extend body.fix-menu;
	.slimScrollDiv,.main-menu-content{
		overflow: visible !important;
	}
	.main-menu{
		width: 150px;
	}
	.nav-item{
	    > a{
			text-align: center;
			padding: 20px 15px !important;
			i{
				display: block;
				font-size: 30px;
				margin-right: 0 !important;
				margin-bottom: 10px;
			}			
			.icon-arrow-down{
				display: none;
			}
		}
	}
	.main-body{
		.page-wrapper{
			margin-left: 150px;
		}
	}
	.main-navigation .nav-item .tree-1,.main-menu-header,.nav-title{
		display: none;
	}
	.main-navigation{		
		transition: all ease-in .3s;
		.nav-item{
			text-align: center;
			i{
				margin-right: 5px;
			}
			.tree-1{
				display: block;
				left: 150px;
				opacity:0;				
				position: absolute;
				top: 0;
				transform: translateY(30px) ;					
				visibility: hidden;
				width: 15em;
				-webkit-transform: translateY(30px);
				background: #34495e;
				transition: all linear 0.3s;
			}
			&:hover	.tree-1{
				-webkit-transform: translateY(0px) ;
				opacity: 1;									
				transform: translateY(0px);
				transition: all linear 0.3s;
				visibility: visible;	
			}
			
			
			.tree-1 li,.tree-1 a{
				text-align: left !important;
			}
		}
		.nav-sub-item{
			.tree-2{
				a{
					padding-left: 30px;
				}							
				-webkit-transform: translateY(30px);
				background-color: #34495E;
				display: block !important;
				opacity: 0;		
				position: absolute;
				top:0;
				left: 210px;
				width: 15em;
				transform: translateY(30px) ;					
				transition: all linear 0.3s;
				visibility: hidden;
				z-index: 9999;			
			}
			&:hover .tree-2{				
				-webkit-transform: translateY(0px) ;
				opacity: 1;									
				transform: translateY(0px);
				transition: all linear 0.3s;
				visibility: visible;				
			}
		}
	}
}
body.menu-bottom{
	@extend body.fix-menu;
	.header-navbar{
		min-height: auto;
	}
	.navbar-wrapper{
		bottom:0;
		top:auto;
	}
	.mega-menu-top{
		.show-notification{
			bottom: 0 !important;
		}		
	}
	.header-notification{
			.show-notification{
				bottom: 65px;
			}
		}	
}
body.menu-rtl{
	@extend body.fix-menu;
	direction: rtl;
	ul{
		padding-right: 0;
	}
	.main-menu{
		right: 0;
	}
	.header-navbar .navbar-wrapper .navbar-logo,.breadcrumb-item{
		float: right;
	}
	.header-navbar .navbar-wrapper .navbar-container{
		margin-left: 0;
	}
	.main-body .page-wrapper{
		margin-left: 0;
		margin-right: 240px;
	}
	.main-body .page-wrapper .page-header-breadcrumb{
		float: left;
	}
	.header-navbar .navbar-wrapper .navbar-container .nav-right .user-profile img{
		margin-left: 10px;
	}
}

body.horizontal-fixed{
	.header-navbar{
		position: fixed;
		z-index: 9999;
		width: 100%;
	}
	.page-header{
		margin-top: 110px;
	}
	.main-menu-header,.main-menu .main-menu-content li.nav-title,
	.main-navigation .nav-item.has-class .tree-1.open,
	.main-menu .main-menu-content .main-navigation .nav-item.has-class .open.tree-2,
	.main-navigation .nav-item.has-class .tree-2.open,
	{
		display: none;
	}
	.main-menu{
		width: 100%;
		position: fixed;
    	top: 64px;
    	z-index: 9999;
	}
	.main-menu .main-menu-content .nav-item > a{
		padding: 20px 15px;
	}
	.main-navigation{
		margin-bottom: 0;
		.nav-item{			
			.tree-1{
			li{
				display: block;
			}							
				-webkit-transform: translateY(30px);
				background-color: #34495E;
				display: block !important;
				opacity: 0;		
				position: absolute;
				top:60px;
				width: 15em;
				left:30px;
				transform: translateY(30px) ;					
				transition: all linear 0.3s;
				visibility: hidden;
				z-index: 9999;			
			}
			&:hover .tree-1{				
				-webkit-transform: translateY(0px) ;
				opacity: 1;									
				transform: translateY(0px);
				transition: all linear 0.3s;
				visibility: visible;				
			}
		}
		.nav-sub-item{
			.tree-2{
				li{
				display: block;
			}							
				a{
					padding-left: 30px;
				}							
				-webkit-transform: translateY(30px);
				background-color: #2C3E50;
				display: block !important;
				opacity: 0;		
				position: absolute;
				top:0;
				width: 15em;
				left: 210px;
				transform: translateY(30px) ;					
				transition: all linear 0.3s;
				visibility: hidden;
				z-index: 9999;			
			}
			&:hover .tree-2{				
				-webkit-transform: translateY(0px) ;
				opacity: 1;									
				transform: translateY(0px);
				transition: all linear 0.3s;
				visibility: visible;				
			}
		}
	}
	.main-body .page-wrapper{
		margin-left: 0;
	}
	.main-menu .main-menu-content .nav-item {
		> a {
		display: inline-block;		
		position: relative;
		margin-left:20px;
		}
	}
	.main-menu .main-menu-content li{
		display: inline-block;
		margin-right: 0;
		border-top:none;
	}
	.main-menu .main-menu-content .nav-item .icon-arrow-down{
		position: relative;
		top:0;
		margin-right: 20px;
		right: -20px;
	}
	.tree-1{
		li{
			display: block;
		}
	}
}
body.horizontal-static{
	@extend body.horizontal-fixed;

	.header-navbar,.main-menu{
		position:initial;
	}

	.page-header{
		margin-top: 60px;
	}
}
body.horizontal-icon{
	@extend body.horizontal-fixed;

	.main-menu .main-menu-content .nav-item{		
		> a{
			text-align: center;
			i{
				display: block;
				margin-bottom:5px;
				margin-right:0px;
			}
		}		
	}
	.main-navigation .nav-item .tree-1{
		top:80px;
	}
	.page-header{
		margin-top: 140px;
	}
}
body.horizontal-icon-fixed{
	@extend body.horizontal-icon;

	.main-menu .main-menu-content .nav-item{		
		> a{			
			i.icon-arrow-down{
				display: none;
			}
		}		
	}

	.header-navbar{
		position: initial;
	}
	.page-header{
		margin-top: 80px;
	}	
}

body.menu-sidebar{
	@extend body.fix-menu;
	.navbar-wrapper{
		position:initial;
	}	
}

// Collapse menu

/*===== Menu layouts css ends ===== */

